*
    margin: 0
    padding: 0
    outline: 0 none
    box-sizing: border-box

body
    font-family: 'Noto Sans', sans-serif
    line-height: 1.6
    margin: 0
    padding: 0
    background: #000
    color: #fff

p
    margin-bottom: 15px

ul
    list-style: none

a
    text-decoration: none
    color: #000
    font-weight: bold
    &:hover
        color: #777

.main
    width: 100%
    position: relative
    .wrapper
        display: flex
        flex-direction: column
        max-width: 760px
        align-items: center
        justify-content: center
        margin: 0 auto
        padding: 80px 40px

        .logo
            display: block
            width: 300px
            height: 300px
            margin-bottom: 40px
            svg
                display: block
                width: 100%
                height: 100%

        h1
            font-size: 50px
            line-height: 110%
            text-align: center
            margin-bottom: 30px
        p
            text-align: center
            line-height: 180%

    @media screen
        @media (max-width: 600px)
            .wrapper
                h1
                    font-size: 30px
                #class-times
                    p
                        font-size: 14px
.container
    width: 80%
    margin: auto
    overflow: hidden


#class-times
    background: #fff
    color: #333
    padding: 40px
    margin: 40px 0

    h2,p
        text-align: left

footer
    background: #111
    color: #fff
    text-align: center
    padding: 10px
    position: absolute
    left: 0
    right: 0
    bottom: 0
    width: 100%